/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: Nunito;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 130rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: Nunito;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

/** UI **/
.header {
  padding: 1rem 0 3rem 0;
  background-color: #FFFFFF;
  box-shadow: 0 0 1rem #e1e1e1;
}
@media (min-width: 768px) {
  .header {
    position: sticky;
    top: 0;
    z-index: 100;
  }
}
@media (min-width: 1024px) {
  .header {
    padding: 0;
  }
}

.contenido-header {
  max-width: 100%;
  margin: 0 auto;
}

.titulo {
  color: #004C84;
  font-weight: 700;
  font-size: 3rem;
  border-bottom: 0.2rem solid #004C84;
  width: max-content;
}

.descripcion {
  font-family: 0.8rem;
}

.footer {
  background-color: #004C84;
  margin-bottom: 0;
}
.footer .navegacion {
  justify-content: center;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 0;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
}
@media (min-width: 1024px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra .logo {
  width: 30rem;
  margin: 0 auto 2rem auto;
  border-bottom: 0.1rem solid #004C84;
}
@media (min-width: 1024px) {
  .barra .logo {
    border-right: 0.1rem solid #004C84;
    border-bottom: none;
  }
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #237BBB;
  text-decoration: none;
  font-size: 1.8rem;
  display: block;
  text-align: center;
  font-weight: 700;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 3rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
@media (min-width: 1024px) {
  .navegacion a {
    margin-right: 6rem;
  }
}
.navegacion a.active {
  background-color: #004C84;
  color: #FFFFFF;
  border-radius: 2rem;
  padding: 0 1rem;
}
.navegacion img {
  width: 3.5rem;
  margin: 0 auto;
}

.icono {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5rem;
  width: 5rem;
  border-radius: 100%;
}
.icono-1 {
  background-color: #68B4EB;
}
.icono-2 {
  background-color: #68EB8C;
}
.icono-3 {
  background-color: #EBAE68;
}
.icono-4 {
  background-color: #E8EB68;
}
.icono-5 {
  background-color: #5DB64F;
}
.icono-6 {
  background-color: #4489BB;
}
.icono-7 {
  background-color: #A569D4;
}
.icono-8 {
  background-color: #CA5B43;
}
.icono-9 {
  background-color: #68EB7D;
}
.icono-10 {
  background-color: #D96757;
}
.icono img {
  height: 60%;
}

@media (min-width: 768px) {
  .accordion-materias {
    display: grid;
    gap: 1rem 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: start;
    grid-auto-rows: auto;
  }
}

.accordion-item {
  height: auto;
  overflow: hidden;
  border: none;
  margin-bottom: 2rem;
}
.accordion-item-materias {
  border-radius: 2rem !important;
  box-shadow: 0 0.3rem 0.2rem #e1e1e1;
}
.accordion-item .accordion-header {
  padding: 1rem;
  margin: 0;
  display: flex;
  align-items: center;
}
.accordion-item .accordion-header .accordion-button {
  font-size: 2rem;
  font-weight: 700;
  color: #004C84;
  background-color: transparent;
}
.accordion-item .accordion-header .accordion-button:focus {
  box-shadow: none;
}
.accordion-item .accordion-header .accordion-button[aria-expanded=true] {
  border-bottom: 0.2rem solid #e1e1e1;
}
.accordion-item .accordion-header .accordion-button::after {
  width: 2rem;
  height: 2rem;
  background-image: url("/src/img/download.svg");
  background-size: 2rem;
}

.accordion-body {
  margin-left: 2rem;
}

.accordion-subject {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.accordion-subject a:hover {
  text-decoration: underline;
}
.accordion-subject a:hover p {
  color: #004C84;
}

.form-check-input {
  width: 2rem;
  height: 2rem;
  border: 0.2rem solid #237BBB;
}

@media (min-width: 1024px) {
  .contenedor-contenido {
    display: flex;
    gap: 2rem;
    height: 60rem;
  }
}
.contenedor-contenido .contenido {
  background-color: #EDF3FE;
  box-shadow: 0 0.5rem 1rem #e1e1e1;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
  padding-bottom: 2rem;
}
.contenedor-contenido .contenido .ruta-contenido {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
  background-color: #EDF3FE;
  padding: 1.5rem;
}
@media (min-width: 1024px) {
  .contenedor-contenido .contenido .ruta-contenido {
    position: sticky;
    top: 0;
  }
}
.contenedor-contenido .contenido .ruta-contenido .icono-guia {
  width: 2rem;
  height: 2rem;
}
.contenedor-contenido .contenido .ruta-contenido .ruta {
  color: #004C84;
  margin: 0;
  font-size: 1.5rem;
  user-select: none;
}
.contenedor-contenido .contenido .llenado-contenido {
  padding: 0 1.5rem;
}
.contenedor-contenido .contenido-recurso {
  width: max-content;
  max-width: 100%;
  margin: 0 auto;
  padding-top: 2rem;
}
.contenedor-contenido .contenido-recurso h2 {
  font-weight: bold;
  font-size: 2.5rem;
}
.contenedor-contenido .contenido-recurso iframe {
  max-width: 100%;
}

.acordeon-contenido {
  overflow-y: auto;
  overflow-x: hidden;
  background-color: #FFFFFF;
  box-shadow: 0 0.5rem 1rem #e1e1e1;
}
@media (min-width: 1024px) {
  .acordeon-contenido {
    border-radius: 2rem;
    width: 40%;
  }
}
.acordeon-contenido .header-contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
  border-bottom: 0.1rem solid #004C84;
}
@media (min-width: 768px) {
  .acordeon-contenido .header-contenido {
    flex-direction: row;
    justify-content: center;
    gap: 3rem;
    padding: 0;
  }
}
@media (min-width: 1024px) {
  .acordeon-contenido .header-contenido {
    justify-content: start;
    padding: 0 2rem;
    gap: 1rem;
  }
}
.acordeon-contenido .header-contenido .titulo-contenido {
  border-bottom: none;
}
@media (min-width: 1024px) {
  .acordeon-contenido .header-contenido .titulo-contenido {
    font-size: 2rem;
  }
}

.accordion-button-contenido {
  color: #000000 !important;
  font-weight: 400 !important;
  border-bottom: 0.05rem solid #e1e1e1;
}
.accordion-button-contenido:focus {
  border-bottom: 0.05rem solid #e1e1e1;
}